:root {
  --h1-fs:1.75rem;
  --par-fs:0.9375rem;
  --btn-fs:0.9375rem;
  --num-fs:1rem;
}

html ::selection {
  background-color: #1683ff;
  color: #ffffff;
}

body {
  background-color: #333;
  color: #fff;
  font-size: 1rem;
}

button[type=submit] {
  display: inline-block;
  border: orange 2px solid;
  border-radius: 5px;
  margin-top: 1rem;
  margin-inline: auto;
}

form {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  border: 1px solid white;
  border-radius: 5px;
  box-shadow: 1px 1px 5px black;
}
form input {
  width: 80%;
  border-radius: 5px;
  border: none;
  color: #333;
}
form input::placeholder {
  opacity: 0.7;
}

.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
  position: relative;
  isolation: isolate;
}

#div {
  box-shadow: 1px 1px 10px black;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  background-color: #fff;
  margin: 0 auto;
}
#div h2 {
  font-size: 1rem;
  text-align: center;
  color: #333;
  text-align: center;
}

.alert {
  margin: 0;
  padding: 0.1rem;
  padding-right: 48px;
}

.alert-dismissible .btn-close {
  padding: 0.5rem;
}

#css {
  padding: 1rem;
  width: max-content;
}

.output {
  position: absolute;
  top: 0;
  left: 0;
}